import React from 'react'
import {Box,Stack,Typography} from '@mui/material';
import HorizontalScrollbar from '../component/HorizontalScrollbar'
import Loader from './Loader';


const similarExercises = ({targetMuscleExercises,equipmentExercises}) => {
  return (
    <Box  sx={{mt:{lg:"100px",xs:'0px'}}}>
      <Box bgcolor="#fff" borderTop="2px solid #ff2625" overflow="scroll">
      <Typography sx={{ fontSize: { lg: '44px', xs: '25px' }, mb:"20px", ml: '20px' }} fontWeight={700} color="#000" mb="33px">
        Similar <span style={{ color: '#FF2625', textTransform: 'capitalize' }}>Target Muscle</span> exercises
      </Typography>
      <Stack direction="row" sx={{ p: 2, position: 'relative' }}  >
        {targetMuscleExercises.length !== 0 ? <HorizontalScrollbar data={targetMuscleExercises} /> : <Loader />}
      </Stack>

      </Box>
      

      <Box borderTop="2px solid #ff2625" overflow="scroll" mt="100px">
      <Typography sx={{ fontSize: { lg: '44px', xs: '25px' }, ml: '20px', mt: { lg: '0px', xs: '60px' } }} fontWeight={700} color="#000" mb="33px">
      Similar <span style={{ color: '#FF2625', textTransform: 'capitalize' }}>Equipment</span> exercises
    </Typography>
    <Stack direction="row" sx={{ p: 2, position: 'relative' }}>
      {equipmentExercises.length !== 0 ? <HorizontalScrollbar data={equipmentExercises} /> : <Loader />}
    </Stack>

      </Box>

     

    </Box>
  )
}

export default similarExercises;

