import React from 'react'
import { Link } from 'react-router-dom';
import {Button,Stack,Typography} from '@mui/material';


const ExerciseCard = ({exercise}) => {
  return (
    <Link className='exercise-card' to={`/exercise/${exercise.id}`}>
        <img src={exercise.gifUrl} alt={exercise.name} loading="lazy" />
        <Stack direction="row" >
            <Button className='btn' sx={{ml:'21px',color:'#fff',backgroundColor:"#ffa9a9",
             fontSize:"14px" ,borderRadius:"20px",textTransform:"capitalize"}}>
            {exercise.bodyPart}

            </Button>

            <Button className='btn' sx={{ml:'21px',color:'#fff',backgroundColor:"#fcc757",
             fontSize:"14px" ,borderRadius:"20px",textTransform:"capitalize"}}>
            {exercise.target}

            </Button>

        </Stack>
        <Typography ml="21px" mt="10px" color="#000" fontWeight="bold" textTransform="capitalize" pb="12px" fontSize="24px">
            {exercise.name}
        </Typography>
    </Link>
  )
}

export default ExerciseCard