import React from 'react'
import { Typography,Stack,Button } from '@mui/material'

import BodyPartImage from '../assets/icons/body-part.png';
import TargetImage from '../assets/icons/target.png';
import EquipmentImage from '../assets/icons/equipment.png';

const Detail = ({exerciseDetail}) => {

  const {bodyPart,gifUrl,name,target,equipment}=exerciseDetail;

  const extraDetail =[
    {
      icon:BodyPartImage,
      name:bodyPart
    },
    {
      icon:TargetImage,
      name:target
    },
    {
      icon:EquipmentImage,
      name:equipment
    }
  ]

  return (
    <Stack gap="60px" 
    sx={{flexDirection:{lg:'row'},p:"20px",alignItems:"center"}}
    >
      <img style={{width:'45%'}} src={gifUrl} alt={name} loading="lazy" className='detail-imagex'/>
      <Stack sx={{gap:{lg:'35px',xs:'20px'},ml:'30px',height:'500px'}}>
        <Typography variant="h3">
          {name}
        </Typography>
        <Typography variant="h5" marginLeft="29px">
          Exercises keep you strong. {name} {`  `} <br/>
           is one of the best exercises to target your {target}.<br/>
            It will help you improve your mood and gain energy.
        </Typography>

        {extraDetail.map((item)=>(
          <Stack key={item.name} direction="row" gap="24px" alignItems="center">
            <Button sx={{background:"#fff2db",width:'80px' ,height:'80px',borderRadius:"50% "}}>
              <img src={item.icon} alt={bodyPart} style={{width:'50px' ,height:'50px'}}/>
            </Button>

            <Typography textTransform="capitalize" variant="h6">
              {item.name}
            </Typography>

            </Stack>

        ))}

      </Stack>

    </Stack>
  )
}

export default Detail